import React from 'react';
import { Carousel, Image, Row } from 'antd';
import { APOD } from '../../api/api';

type ResultPanelProps = {
  images: APOD[] | null
};
const imageStyle: React.CSSProperties = {
  margin: 0,
  height: 'max-content',
  color: '#000',
  textAlign: 'center',
  background: 'var(--background-default)',
};

const ResultPanel: React.FC<ResultPanelProps> = ({ images }) => {
  return (
    images === null || images.length === 0 ?
      <Row justify={'center'}>
        <Image
          width={200}
          height={200}
          src="error"
          fallback="data:image/png;base64,"
        />
      </Row>
      :
      <Carousel >
        {images.map((image, index) =>
          <div key={index}>
            <h3 style={imageStyle}>
              <Image
                height={300}
                src={image.url}
                fallback="data:image/png;base64,"
              />
            </h3>
          </div>
        )}
      </Carousel>
  );
};

export default ResultPanel;